import { ResourceManager } from '@/activity/core/resourceManager'
import { PaintResource } from '@/activity/core/resource/viewer/paintResource'
import * as PaintProduct from '@/activity/core/resource/product/paintProduct'
import { SceneBase } from '@/activity/core/sceneBase'
import { Intro } from '@/activity/scene/intro'
import gsap from 'gsap'
import Config from '@/activity/gameUtil/config'
import Util from '@/activity/gameUtil'
import {
  getSymbol,
  shuffleArray,
  soundPlay,
  syllableToNum
} from '@/activity/gameUtil/game'
import { Eop } from '@/activity/scene/eop'
import { Dimmed } from '@/activity/widget/dimmed'
import { EventSprite } from '@/activity/widget/eventSprite'
import { Affordance } from '@/activity/widget/affordance'
import { App } from '@/activity/core/app'
import config from '@/activity/gameUtil/config'
import { Rectangle } from 'pixi.js'

const btnColor = [
  0xf92121 /**빨강 */,
  0xff8f29 /**주황 */,
  0xffe424 /**황토 */,
  0x23cc23 /**녹색 */,
  0x2da2ea /**파랑 */,
  0x8a4dff /**보라 */
]
const btnName = [
  'btn_red',
  'btn_orange',
  'btn_yellow',
  'btn_green',
  'btn_blue',
  'btn_violet'
]

const viewerApi = `${config.restAPI}viewer/paint/sounds/`
const commonApi = `${config.restAPI}viewer/paint/sounds/`

// 보기에 들어가는 색버튼
export class ColorBtn extends PIXI.Sprite {
  private mNormalTexture: PIXI.Texture
  private mPickTexture: PIXI.Texture
  private mDisableTexture: PIXI.Texture

  private mClickFlag: boolean
  private mActive: boolean
  set active(v: boolean) {
    this.mActive = v
    if (v === false) {
      this.texture = this.mDisableTexture
    }
  }
  private mBtnColor: number
  get btnColor(): number {
    return this.mBtnColor
  }

  constructor(id: number, texture: string) {
    super()
    this.mActive == true
    this.mBtnColor = btnColor[id]
    this.mNormalTexture = ResourceManager.Handle.getViewerResource(
      `${texture}.png`
    ).texture
    this.mPickTexture = ResourceManager.Handle.getViewerResource(
      `${texture}_pick.png`
    ).texture
    this.mDisableTexture = ResourceManager.Handle.getViewerResource(
      `btn_gray.png`
    ).texture
    this.texture = this.mNormalTexture
    this.anchor.set(0.5)

    this.mClickFlag = false

    this.on('pointerdown', () => {
      this.mClickFlag = true
      this.changeTexture(true)
    })
    this.on('pointerup', () => {
      if (this.mClickFlag) {
        this.onClick()
      }
      this.mClickFlag = false
    })
    this.on('pointerout', () => {
      this.changeTexture(false)
      this.mClickFlag = false
    })
  }
  interactiveMode(flag: boolean) {
    /*
     *this.mActive가 true 이면 게임 진행 중
     *this.mActive가 false 이면 게임끝 destroy
     */
    if (this.mActive == true) {
      this.interactive = flag
      this.buttonMode = flag
      this.changeTexture(!flag)
    } else {
      this.interactive = false
      this.buttonMode = false
      this.texture = this.mDisableTexture
    }
  }
  onClick() {
    /** */
  }

  changeTexture(flag?: boolean) {
    if (flag) {
      this.texture = this.mPickTexture
    } else {
      this.texture = this.mNormalTexture
    }
  }
}
export class Game extends PIXI.Container {
  /**어포던스 손 */
  private mAffordance: Affordance
  // 인트로 type OR 게임 type
  private mType: string
  // 색버튼카드 배열
  private mColorAry: Array<ColorBtn>
  // 퀴즈가 들어가는 스케치북
  private mSketchbook: PIXI.Container
  // 인트로 시작할 때 쓰는 퀴즈데이터
  private mIntroQuiz: PIXI.Sprite

  // 3초 비활성화시 퀴즈 깜빡
  private mDelayMotion: any

  /**자음 모음 하나씩 그림 */
  private mQuizElement: Array<PIXI.Sprite>
  /**자음 모음 하나씩 테두리 */
  private mLineElement: Array<PIXI.Sprite>
  /**정답 색 배열 */
  private mCorrectColor: Array<number>
  // 현재 퀴즈 순서(진행도)
  private mQuizStep: number
  set quizStep(v: any) {
    console.log(`현재 진행스텝${v}, 총 퀴즈스텝${this.mCorrectColor.length}`)
    if (v >= this.mCorrectColor.length) {
      // 모든 퀴즈를 완료했을 때 모션
      // 퀴즈 카드가 나오고 사운드 나오고 읽어주고 나가기
      this.outro()
    } else {
      this.mQuizStep = v
      soundPlay(`ac3_cardpop`)
      this.blink()
    }
  }

  constructor(type: string) {
    super()
    this.mType = type

    this.mColorAry = []
    this.mQuizStep = 0

    const bg = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource(`bg.png`).texture
    )
    this.addChild(bg)

    this.mSketchbook = new PIXI.Container()
    this.mSketchbook.position.set(config.w / 2, config.h / 2 - 30)
    this.addChild(this.mSketchbook)
  }
  async onStart() {
    if (this.mType == 'intro') {
      // 인트로를 시작 할때
      this.createIntro()
    } else {
      // 본 게임 시작할때
      await this.createWord()
      this.quizStep = 0
    }
    // 선택하는 색버튼 생성
    await this.createColorBtn()
  }
  // 인트로 데이터 생성
  private createIntro() {
    this.mIntroQuiz = new PIXI.Sprite(
      ResourceManager.Handle.getProductResource(
        `day${Config.studyDay}.png`
      ).texture
    )
    this.mIntroQuiz.anchor.set(0.5)
    this.mIntroQuiz.position.set(
      this.mSketchbook.width / 2,
      this.mSketchbook.height / 2
    )
    this.mSketchbook.addChild(this.mIntroQuiz)
    this.mIntroQuiz.tint = 0xbcbcbc
  }

  // 마지막 숫자를 받아서 섞었을때 마지막 숫자가 앞으로 안오게 섞는 배열
  reShuffle(num: number): Array<number> {
    let array = []
    array = shuffleArray([0, 1, 2, 3, 4, 5])
    if (array[0] == num) {
      this.reShuffle(num)
      return
    } else {
      return array
    }
  }
  // 퀴즈 단어 및 음절을 만든다
  private createWord(): Promise<void> {
    return new Promise((resolve) => {
      this.mQuizElement = []
      this.mLineElement = []
      this.mCorrectColor = []

      /*
       * ex)
       *quizWord: '아앉'
       *quizAry" ['ㅇ','ㅏ','ㅇ','ㅏ','ㄵ']
       */
      let colorIdx = shuffleArray([0, 1, 2, 3, 4, 5])

      for (let i = 0; i < Config.quizWord.length; i++) {
        // 한 음절을 자음,모음으로 쪼개주는 함수
        const quizAry = Util.Game.getConstantVowel(Config.quizWord[i])

        let type = 0
        // 한 음절의 타입을 알아내는 함수
        type = Util.Game.getCombinationType(Config.quizWord[i])
        console.log(`${i}번째 타입 type${type}`)

        const syllable = new PIXI.Container()
        syllable.x = i * 210

        for (let j = 0; j < quizAry.length; j++) {
          this.mCorrectColor.push(btnColor[colorIdx[0]])

          if (colorIdx.length == 1) {
            const finalNum = colorIdx[0]
            colorIdx.splice(0, 1)
            colorIdx = this.reShuffle(finalNum)
          } else {
            colorIdx.splice(0, 1)
          }

          const quiz = new EventSprite(
            ResourceManager.Handle.getProductResource(
              `day${Config.studyDay}_${i + 1}_${j + 1}.png`
            ).texture
          )
          // quiz.tint = 0xbcbcbc
          this.mQuizElement.push(quiz)

          const line = new EventSprite(
            ResourceManager.Handle.getProductResource(
              `guideLine${Config.studyDay}_${i + 1}_${j + 1}.png`
            ).texture
          )
          line.tint = this.mCorrectColor[j + i * (quizAry.length - 1)]
          this.mLineElement.push(line)

          syllable.addChild(quiz, line)
          if (j == quizAry.length - 1) {
            this.mSketchbook.addChild(syllable)
          }
          // debugLine(syllable)
        }

        this.mSketchbook.position.set(
          Config.w / 2 - this.mSketchbook.width / 2,
          Config.h / 2 - this.mSketchbook.height / 2 - 30
        )
      }

      this.mSketchbook.interactive = true
      this.mSketchbook.buttonMode = true
      this.mSketchbook.on('pointertap', async () => {
        this.mSketchbook.interactive = false
        await this.clickWord()
        this.mSketchbook.interactive = true
      })
      this.blink()
      resolve()
    })
  }
  destroyMotion(): Promise<void> {
    return new Promise<void>((resolve) => {
      if (this.mDelayMotion) {
        this.mDelayMotion.kill()
        this.mDelayMotion = null
        for (const line of this.mLineElement) {
          line.alpha = 1
          line.scale.set(1)
        }
      }
      resolve()
    })
  }
  /**3초 동안 비활성이면 깜빡 */
  async blink() {
    await this.destroyMotion()
    this.mDelayMotion = gsap.timeline({})
    this.mDelayMotion.to(this.mLineElement[this.mQuizStep], {
      alpha: 0,
      duration: 0.25
    })
    this.mDelayMotion.to(this.mLineElement[this.mQuizStep], {
      alpha: 1,
      duration: 0.25
    })
    this.mDelayMotion.to(this.mLineElement[this.mQuizStep], {
      alpha: 0,
      duration: 0.25
    })
    this.mDelayMotion.to(this.mLineElement[this.mQuizStep], {
      alpha: 1,
      duration: 0.25
    })
    this.mDelayMotion.eventCallback('onComplete', () => {
      gsap.delayedCall(3, () => {
        this.blink()
      })
    })
  }
  /**퀴즈 단어를 클릭하면 깜빡인다 */
  clickWord(): Promise<void> {
    return new Promise<void>((resolve) => {
      this.destroyMotion()

      this.blink()
      resolve()
    })
  }

  /**컬러버튼 생성 */
  private createColorBtn(): Promise<void> {
    return new Promise((resolve) => {
      const colorBox = new PIXI.Container()
      soundPlay(`ac3_cardpop`)

      if (this.mType == 'intro') {
        console.log(`데이터로 코드 수정 필요`)
        // const study = getSymbol()
        const study = `ㅏ`
        const num = syllableToNum(study)
        const sound = `2jung_${num}`

        gsap.delayedCall(1, () => {
          soundPlay(sound, true)
        })
      }

      let offSetx = 0
      for (let i = 0; i < 6; i++) {
        const colorBtn = new ColorBtn(i, btnName[i])
        if (offSetx == 0) {
          offSetx = colorBtn.width / 2
        }
        colorBtn.position.set(offSetx, colorBtn.height / 2)
        offSetx += colorBtn.width + 40
        this.mColorAry.push(colorBtn)
        colorBox.addChild(colorBtn)
        colorBtn.alpha = 0
        colorBtn.active = false

        /*
         *사운드 플레이 되는 동안 딜레이 코드
         *색버튼 등장음
         */

        gsap
          .to(colorBtn, { alpha: 1, duration: 1 })
          .eventCallback('onComplete', () => {
            gsap.delayedCall(1, () => {
              colorBtn.active = true
              colorBtn.interactiveMode(true)
              resolve()
            })
          })
        /**컬러버튼 클릭이벤트 코드 */
        colorBtn.onClick = () => {
          this.selectColor(colorBtn)
        }
      }

      colorBox.pivot.set(colorBox.width / 2, colorBox.height / 2)
      colorBox.position.set(Config.w / 2, Config.h - colorBox.height)
      this.addChild(colorBox)

      this.mAffordance = new Affordance(colorBox, false)
      colorBox.addChild(this.mAffordance)
      colorBox.interactive = true
      colorBox.hitArea = new Rectangle(0, 0, colorBox.width, colorBox.height)

      colorBox.on('pointerup', async () => {
        colorBox.interactive = false
        if (this.mAffordance) {
          await this.mAffordance.resetTimer()
        }
        colorBox.interactive = true
      })
    })
  }

  // 색버튼을 선택했을때 모션 및 정오답 결정
  async selectColor(colorBtn: ColorBtn) {
    this.setColorInteractive(false)

    // 정오답 없는 인트로 퀴즈

    if (this.mType == 'intro') {
      /**정답 사운드 */
      soundPlay(`ac3_correct`)

      // 음절 깜빡이는 효과
      gsap
        .to(this.mIntroQuiz, { alpha: 0, duration: 0.25 })
        .eventCallback('onComplete', () => {
          this.mIntroQuiz.tint = colorBtn.btnColor
          gsap
            .to(this.mIntroQuiz, { alpha: 1, duration: 0.25 })
            .repeat(4)
            .yoyo(true)
            .eventCallback('onComplete', () => {
              console.log(`데이터로 코드 수정 필요`)
              // const study = getSymbol()
              const study = `ㅏ`
              const num = syllableToNum(study)
              const sound = `2jung_${num}`
              soundPlay(sound, true)
              gsap.delayedCall(1, () => {
                this.endGame()
              })
            })
        })
    } else {
      // 본게임 정오답 분기
      if (colorBtn.btnColor == this.mCorrectColor[this.mQuizStep]) {
        /**정답 사운드 */
        soundPlay(`ac3_correct`)

        /**정답 모션 */
        await this.correctMotion()
        this.quizStep = this.mQuizStep + 1
        if (this.mQuizStep < this.mCorrectColor.length) {
          // 비활성화된 색버튼 reset
          this.setColorInteractive(true)
        }
      } else {
        // 오답 일때
        soundPlay(`ac3_wrong`)
        console.log(`오답처리`)
        colorBtn.active = false
        await this.blink()
        // await this.wrongMotion()
        this.setColorInteractive(true)
      }
    }
  }
  /**
   * 본게임의 모든 퀴즈를 완료했을 때 모션
   * Outro
   */
  private outro() {
    soundPlay(`ac3_cardpop`)
    this.blink = () => null
    this.destroyMotion()
    this.mAffordance.reset()
    this.removeChild(this.mAffordance)
    this.mAffordance = null
    for (const introColor of this.mColorAry) {
      introColor.interactive = false
    }
    /**딤드 생성 */
    const dimmed = new Dimmed(Config.w, Config.h)
    dimmed.alpha = 0
    this.addChild(dimmed)

    gsap
      .to(dimmed, { alpha: 0.4, duration: 0.5 })
      .eventCallback(`onComplete`, () => {
        /**카드 생성 */
        const card = new EventSprite(
          ResourceManager.Handle.getViewerResource(`card.png`).texture
        )
        card.position.set(Config.w / 2, Config.h / 2)
        card.alpha = 0
        this.addChild(card)
        gsap.to(card, { alpha: 1, duration: 0.5 })
        /**텍스트 생성 */
        const text = new PIXI.Text(getSymbol(), {
          fontSize: 350,
          fontFamily: 'TmoneyRoundWindExtraBold'
        })
        text.pivot.set(text.width / 2, text.height / 2)
        text.position.set(-246, 0)
        // text.position.set(250, card.height / 2)
        /**그림 생성 */
        const completeCard = new EventSprite(
          ResourceManager.Handle.getProductResource(
            `day${Config.studyDay}_complete.png`
          ).texture
        )
        completeCard.position.set(188, -58)
        const completeText = new PIXI.Text(Config.quizWord, {
          fontSize: 100,
          fontFamily: 'TmoneyRoundWindExtraBold'
        })
        completeText.anchor.set(0.5)
        completeText.position.set(188, 158)

        card.addChild(text, completeCard, completeText)

        gsap.delayedCall(1, async () => {
          /**complete 사운드 재생 */
          soundPlay(`word_${Config.studyDay}`, true)
        })

        this.destroyMotion()
        gsap.delayedCall(3, async () => {
          const EOP = new Eop()
          this.addChild(EOP)
          await EOP.start()
          this.endGame()
        })
      })
  }
  // 정답시 모션
  // 타이머 끄고 다음퀴즈를 위해 비활성화된 색버튼 reset
  private correctMotion(): Promise<void> {
    return new Promise<void>((resolve) => {
      for (const color of this.mColorAry) {
        color.active = false
      }
      /**회색 글자 사라지고 */
      gsap
        .to(this.mQuizElement[this.mQuizStep], { alpha: 0, duration: 0.25 })
        .eventCallback('onComplete', () => {
          this.mQuizElement[this.mQuizStep].tint = this.mCorrectColor[
            this.mQuizStep
          ]
          /** 정답 색글자 나타나고 */
          gsap
            .to(this.mQuizElement[this.mQuizStep], { alpha: 1, duration: 0.25 })
            .repeat(4)
            .yoyo(true)
            .eventCallback('onComplete', () => {
              this.setColorInteractive(true, 'start')
              resolve()
            })
        })
    })
  }
  // 오답 모션
  // 정답인 음절 확대 모션 및 사운드 재생
  private wrongMotion(): Promise<void> {
    return new Promise<void>((resolve) => {
      gsap.delayedCall(1, () => {
        resolve()
      })
    })
  }
  /*
    컬러버튼의 interactive 조정
    correctMotion & selectColor 에서 사용
  */
  setColorInteractive(flag: boolean, reset?: string) {
    for (const colorBtn of this.mColorAry) {
      if (reset) {
        colorBtn.active = true
      }
      colorBtn.interactiveMode(flag)
    }
  }

  // 게임이 끝났을 경우
  endGame() {
    this.removeChildren()
    // 인트로 끝나고 본게임 넘어갈 때
    if (this.mType == 'intro') {
      ;(this.parent as Paint).next()
    } else {
      // 본게임 끝나고 홈화면으로 나갈때
      this.removeChildren()
      ;(this.parent as Paint).endGame()
    }
  }
}
export class Paint extends SceneBase {
  /*
    인트로(미니게임)과 본게임을 flag으로 실행
    flag true => 인트로(미니게임)
    flag false => 본게임
    미니게임 끝난 후 본게임 시작하는 로직
  */
  private mIntroScene: Game

  constructor() {
    super('Paint')
  }
  async onInit() {
    await App.Handle.showLoadingScreen()

    await this.loadViewerResource(PaintResource)
    await this.loadProductResource(PaintProduct[`day${Config.studyDay}`])

    await App.Handle.closeLoadingScreen()

    console.log(this.gamename)
  }

  async onStart() {
    console.log(this.gamename)

    /*
    게임 타이틀 나오는 인트로 화면
    */
    const intro = new Intro()
    this.addChild(intro)
    await intro.intro()
    this.removeChild(intro)

    const bgm = new Audio()
    bgm.src = `${config.restAPI}viewer/paint/sounds/ac3_bgm.mp3`
    bgm.play()
    bgm.loop = true
    window['currentBGM'] = bgm
    this.removeChildren()

    // 인트로 (미니게임) 시작
    this.mIntroScene = new Game(`intro`)
    this.addChild(this.mIntroScene)

    this.mIntroScene.onStart()
  }
  // 인트로(미니게임)이 끝나면 this.mIntroScene.onEnd()에서 next를 활성화
  // 인트로(미니게임)이 destroy 본게임 생성
  next() {
    this.mIntroScene.removeChild()
    this.mIntroScene = null
    this.removeChildren()

    // this.x = Config.w

    this.mIntroScene = new Game(`game`)
    this.addChild(this.mIntroScene)

    // gsap.to(this, { x: 0, duration: 0.5 }).eventCallback(`onComplete`, () => {
    this.mIntroScene.onStart()
    // })
  }
  // 본게임이 끝난 뒤 홈화면으로 이동
  //Game에서 close버튼을 누르거나 본게임이 끝났을때 활성화
  endGame() {
    console.log(`THE END`)
    this.mIntroScene.removeChildren()
    this.removeChildren()

    App.Handle.nextGame()
  }
}
